<template>
    <div>
        <loader v-if="loading" />
        <div class="card m-b-30">
            <div class="card-body">
                <h2>Hush <a href="http://stage.cast.i.ng/project/addrole/9" class="mdb role-add float-right">Add Role</a>
                </h2>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import Loader from '../template/loader';
    export default {
        name: 'manageRole',
        data() {
            return {
                loading: true,
                editRoleData: "",
                token: '',
                applicantID: '',
                siteUrl: "http://stage.cast.i.ng/",
            };
        },
        components: {
            loader: Loader,
        },
        mounted() {
            let roleID = this.$route.params.id;

            // this.detailID = roleID;

            this.token = JSON.parse(localStorage.getItem('token'));
            console.log(this.token);


            var config = {
                headers: {
                    'Access-Control-Allow-Origin': '*'
                }
            };

            let userID = JSON.parse(localStorage.getItem('token'));

            axios({
                method: "GET",
                "url": 'https://api.cast.i.ng/projectrole/applicants/' + roleID,
                config
            }).then(result => {

                this.editRoleData = result;
            }, error => {

                console.log('API CALL FAILED');
                console.error(error);
            });


            this.loading = true;
            axios.get('https://jsonplaceholder.typicode.com/todos/1').then(
                response => {
                    this.loading = false;
                    console.log('Page Changes');
                },
                error => {
                    this.loading = false;
                    console.log('Page Error');
                }
            );
        },
    };
</script>

<style>
</style>


